.slider-wrapper {
  border: 1px solid;
  border-radius: 15px;
  background: #EDEDF7;
  max-height: 344px;
}

.slider-img img {
  width: 366px;
  height: 342px;
  border-radius: 15px 0 0 15px;
}

.slider-text {
  width: 800px;
  font-weight: bold;
  font-size: 28px;
  line-height: 42px;
  margin-bottom: 36px;
  background: linear-gradient(45deg, #F0145A 10%, #EF8080 90%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: transparent;
}

.slider-item {
  display: grid !important;
  grid-template-columns: 400px 920px;
}

.slider-team {
  display: inline-flex;
  position: relative;
  margin-bottom: 10px;
  margin-top: 34px;
  margin-left: 2px;
}

.slider-team a {
  font-size: 14px;
  color: #24A3FF;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 0.08em;
}

.slider-team a svg {
  position: absolute;
  top: 2.5px;
}

.slider-person {
  color: #B3B3BA;
  font-weight: bold;
  margin-bottom: 15px;
}

.slider-position {
  color: #B3B3BA;
}

.slider-team:hover svg path {
  stroke: #F0145A;
}

.slider-team a:hover {
  color: #F0145A;
  transition: ease 0.3s;
}

.slick-dots {
  position: absolute;
  left: 47%;
  text-align: center;
  margin-top: 20px;
  margin-bottom: 64px;
}

.slick-dots li {
  display: inline-block;
  margin: 0 5px;
}

.slick-dots button {
  font-size: 0;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #24A3FF;
  opacity: 0.5;
  border: none;
  cursor: pointer;
}

.slick-dots button:hover {
  background-color: #0085FF;
  opacity: 0.8;
}

.slick-dots .slick-active button {
  background-color: #006CCF;
  opacity: 1;
}

